.item {
  background-color: white;
}

.img-resp {
  max-width: 190px;
  width: 100%;
  height: auto;
}

.imdb {
  background-color: #f3ce13;
  color: black;
}

.scores, .station {
  width: 100%;
}

@media (width < 576px) {
  .item {
    min-width: 105px;
    width: 30%;
  }
}

@media (576px < width <= 768px) {
  .item {
    width: 30%;
  }
}

@media (768px < width <= 992px) {
  .item {
    background-color: white
  }
}

@media (width > 992px) {
  .item {
    background-color: white;
  }
}